<h2>简介</h2>
<p><code>VUX</code>（读音 [v’ju:z]，同 <code>views</code>）是基于<code>WeUI</code>和<code>Vue</code>(2.x)开发的移动端UI组件库，主要服务于微信页面。</p>
<p>基于<code>webpack + vue-loader + vux</code>可以快速开发移动端页面，配合<code>vux-loader</code>方便你在<code>WeUI</code>的基础上定制需要的样式。</p>
<p><code>vux-loader</code>保证了组件按需使用，因此不用担心最终打包了整个vux的组件库代码。</p>
<p><code>vux</code>并不完全依赖于<code>WeUI</code>，但是尽量保持整体UI样式接近<code>WeUI</code>的设计规范。</p>
<p>即使你不直接使用 VUX 组件代码, 你依然可以参考 VUX 代码来实现自己的组件。</p>
<div class="warning">
<p>vux@2.x 推荐<code>webpack+vue-loader</code>方式的开发，如果要使用<code>umd</code>文件，请参照<router-link to="/zh-CN/install/umd.html">文档</router-link>。不建议使用引入<code>script</code>的方式进行开发，因为它会带来一系列的开发、维护、效率、部署问题。<br><br>
<strong>Life is short, use webpack.</strong></p>
